import React from 'react'
// import * as axios from "axios";
import abt from './aboutHotel.module.css'
import Galary from '../galary/Gelary';


function AboutHotel(props) {

    // if (!props.sStyle) {
    //     return <div> ...</div>
    // }
    // debugger
    // if (!props.sAbout) {
    //     return <div>   ...</div>
    // }

    if (!props.sStyle) {
        return <div> ...</div>
    }

    // if (!props.sRoom) {
    //     return <div>  ...</div>
    // }
// debugger
    let aboutElements = props.aboutUsItems.map(about => (
        <div key="about.id">
            <div>

                <h3>
                    {about.title}
                </h3>
                <p>
                    {about.text}
                </p>
            </div>
            <div style={{ width: '100%' }}>
                <img width='100%' height='450px'/*className={abt.image}*/ src={about.aboutImg} alt={about.title} />
            </div>
            <div>
                <h3></h3>
                <p>           ,      .          .            .      ,    .      ,            .     , ,     ,      .</p>
            </div>

        </div>
    )
    )

    return (
        <div>
            {aboutElements}
            <h3> </h3>
            <Galary sStyle={props.sStyle}/>

        </div>
    )

}



export default AboutHotel
