import React from 'react'
import Slider from "react-slick";
import { SRLWrapper } from 'simple-react-lightbox';
import FRS from './FRSlider.module.scss'
// import * as axios from "axios";


function FRSlider(props) {

    // {/* ${props.styleItems.map(st => st.subject)} lighten-1}`*/} 
    const settings = {
        dots: true,                 // 
        focusOnSelect: false,       //       
        fade: false,                //    true
        lazyLoad: true,             //  
        pauseOnHover: true,         //   
        infinte: true,              //  
        speed: 750,                 //  
        slidesToShow: 1,            //    
        arrows: false,              //   
        slidesToScroll: 1,          //     
        className: "roomSlides",    //   
        autoplay: false,            //  
        //autoplaySpeed: 7000,        //          
        appendDots: dots => (
            <div
                style={{
                    borderRadius: "10px",
                    padding: "10px"
                }}
            >
                <ul className={FRS.styleLi} style={{ margin: "0px" }}> {dots} </ul>
            </div>
        ),
        customPaging: i => (
            <div className={`${FRS.dotsRooms} ${props.sStyle.map(s => s.subject)} lighten-1`} >
            </div>
        )
    }
    // debugger
    return (
//     !!!
        <Slider {...settings}>
            {props.sRoom.fullRoom.img.imgBigSlid.map(sl => <SRLWrapper>
                <div className={FRS.sliderImgBig} >

                    <a href={sl.img}>
                        <div className={FRS.sliderImg}>
                            <img width='100%' src={sl.img} alt="" />
                        </div>
                    </a>
                </div>
            </SRLWrapper>)}

        </Slider>

    );
}
export default FRSlider