import * as axios from 'axios'
import { NavLink } from 'react-router-dom'
import Slider from 'react-slick'
import serv from './service.module.scss'



function Service(props) {



   if (props.serviceItems.length === 0) {
      axios.get('http://localhost:3333/services').then(response => {
         props.serviceHot(response.data)
      })
   }
   if (props.styleItems.length === 0) {
      axios.get('http://localhost:3333/thems').then(response => {
         props.styleHot(response.data)
      })
   }

   const settings = {
      dots: true,
      focusOnSelect: true,//       
      fade: false, //   true
      lazyLoad: true, // 
      pauseOnHover: true, //  
      infinte: true, //  
      speed: 750, // 
      slidesToShow: 2, //   
      // arrows: true, //  
      nextArrow: <div className={serv.imgPrew}>2</div>,
      prevArrow: <div className="raz">1</div>,
      slidesToScroll: 1, //    
      className: "servicesMiniSlides", //  
      autoplay: false, // 
      autoplaySpeed: 1000,//         
      // centerMode: true
      responsive: [
         {
            breakpoint: 1050,
            settings: {
               slidesToShow: 2
            }
         },
         {
            breakpoint: 715,
            settings: {
               slidesToShow: 1
            }
         }
      ]
   }

   let slidService = props.serviceItems.map(ss => <NavLink to={`services/${ss.linkMore}`} key={ss.id}><div style={{ width: `100%`, maxWidth: `340px` }}>
      <div className={serv.imgPrew} style={{ backgroundImage: `url(${ss.img})`, height: `200px` }}></div>
      <div>
         <div><h3>{ss.nameService}</h3></div>
         <div>{ss.about.mini.map(s=><p>{s.text}</p>)}</div>
         <div>{ss.termsOfService}</div>
      </div>
   </div>
   </NavLink>
   )

   return (
      <div>
         <h2></h2>
         <Slider {...settings}>
            {slidService}
         </Slider>
      </div>

   )
}


export default Service



